footer {
	position: relative;
	color: white;
	padding: 50px 0;
	margin-top: 5%;
	h1, h2, h3, h4, h5 {
		@extend .no-margin; }
	img {
		position: absolute;
		right: 15%;
		bottom: -30%;
		width: 20%; }
	.btn {
		color: $blue;
		margin-top: 15px;
		&:focus,
		&:hover {
			color: $blue; } } }
@media (max-width: 768px) {
	footer {
		margin-top: 1%; } }
