html, body {
	background-color: none;
	margin: 0;
	height: 100%;
	font-family: $font-light; }
@media (max-width: 768px) {
	html, body {
		height: auto; } }
.initial {
	display: initial; }
.no-border {
	border: none; }
.no-margin {
	margin: 0; }
.no-padding {
	padding: 0; }
.bg-blue {
	@include background-color($blue); }
.bg-inner {
	@include background-color($inner);
	.thumbnail {
		background-color: transparent; } }
.text-light {
	font-family: $font-light; }
.text-medium {
	font-family: $font-medium; }
.text-bold {
	font-family: $font-bold; }
.text-success {
	color: $green; }
.text-primary {
	color: darken($blue, 20%); }
.text-info {
	color: lighten($blue, 20%); }
.text-danger {
	color: $red; }
.text-black {
	color: black; }
.font-bold {
	font-weight: bold; }
.font-mf {
	font-family: $font-mf; }
section {
	padding: 50px 0; }
