header {
	position: relative;
	// min-height: 100%
	color: white;
	max-width: 100%;
	overflow: hidden;
	padding: 0 0 50px;
	.navbar {
		padding: 30px 0;
		.navbar-brand {
			height: auto;
			margin-left: -15px; } }
	.navbar-transparent {
		background-color: transparent;
		border: none; }
	.bg-radius {
		&:before {
			position: absolute;
			content: '';
			top: -75%;
			right: 0;
			width: 50%;
			height: 250%;
			background-color: transparentize($inner, .5);
			@include border-radius-separate(100%, 0, 100%, 0); } }
	.btn {
		color: $blue;
		&:focus,
		&:hover {
			color: $blue; } }
	.grid-table {
		position: absolute;
		top: 50%;
		@include transform(translate(0, -50%)); } }
.vertical-align {
	display: flex;
	align-items: center;
	iframe {
		margin-top: 50px; } }
@media (max-width: 768px) {
	header {
		height: auto;
		padding-top: auto;
		padding-bottom: 0;
		.bg-radius {
			margin-top: 50px;
			text-align: left;
			border-bottom: 1px solid black;
			&:before {
				display: none; } }
		.grid-table {
			position: relative;
			margin-top: 45%;
			.grid-cell {
				vertical-align: top; } } }
	.vertical-align {
		display: initial; } }
@media (max-width: 480px) {
	header {
		.grid-table {
			margin-top: 80%; } } }
@media (max-width: 375px) {
	header {
		.grid-table {
			margin-top: 100%; } } }
@media (max-width: 320px) {
	header {
		.grid-table {
			margin-top: 120%; } } }
